import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue'
import otherLogin from '../../components/home/other-login.vue'
export default {
	data() {
		return {
			status:false,//false代表账号密码登录，true是手机验证码登录
			disabled:true,
			loading:false,
			username:'',
			password:'',
			phone:'',
			verification:"",
			codetime:0
		}
	},
	watch:{
		username(){
			this.onBtnChange()
		},
		password(){
			this.onBtnChange()
		},
		phone(){
			this.onBtnChange()
		},
		verification(){
			this.onBtnChange()
		}
	},
	methods: {
		// 验证手机号码
		isPhone(){
			let mPattern = /^1[34578]\d{9}$/; 
			return mPattern.test(this.phone);
		},
		// 获取验证码
		getVerification(){
			if(this.codetime > 0){
				uni.showToast({
					title:'不能重复获取',
					icon:'none'
				})
				return 
			}
			// let mPattern = /^1[34578]\d{9}$/; 
			// 验证手机合法性
			// if(!mPattern.test(this.phone)){
			if(!this.isPhone()){
				uni.showToast({
					title:'请输入正确手机号',
					icon:'none'
				})
				return 
			}
			// 请求服务器，发送验证码
			// 发送成功,开启倒计时
			this.codetime =10
			let timer = setInterval(()=>{
				this.codetime--;
				if(this.codetime < 1){
					clearInterval(timer)
					this.codetime = 0
				}
			},1000)
		},
		// 改变按钮状态
		onBtnChange(){
			console.log(this.phone)
			console.log(this.verification)
			if((this.username && this.password)||(this.phone && this.verification)){
				this.disabled = false
				return 
			}
			this.disabled = true
		},
		// 返回上一步
		back(){
			
		},
		// 提交登录
		submit(){
			// 账号密码登录
			if(!this.status){
				
			}else{
				// 手机登录时，验证手机合法性
				if(this.isPhone()){
					uni.showToast({
						title:'请输入正确手机号',
						icon:'none'
					})
					return 
				}
			}
		},
		// 切换登录状态,一旦切换登录状态就清空表单
		changeStatus(){
			this.initInput()
			this.status = !this.status
		},
		// 初始化表单
		initInput(){
			this.username=''
			this.password=''
			this.phone=''
			this.verification=''
		}
	},
	components:{
		uniStatusBar,
		otherLogin
	}
}
